body {
  background-color: #f9f9f9;
  flex-wrap: wrap;
  place-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Raleway;
  display: flex;
}

.payment-title {
  text-align: center;
  width: 100%;
}

.form-container .field-container:first-of-type {
  grid-area: name;
}

.form-container .field-container:nth-of-type(2) {
  grid-area: number;
}

.form-container .field-container:nth-of-type(3) {
  grid-area: expiration;
}

.form-container .field-container:nth-of-type(4) {
  grid-area: security;
}

.field-container input {
  box-sizing: border-box;
}

.field-container {
  position: relative;
}

.form-container {
  grid-column-gap: 10px;
  color: #707070;
  grid-template: "name name" 90px
                 "number number" 90px
                 "expiration security" 90px
                 / auto auto;
  max-width: 400px;
  padding: 20px;
  display: grid;
}

label {
  padding-bottom: 5px;
  font-size: 13px;
}

input {
  border: 1px solid #dcdcdc;
  border-radius: 3px;
  width: 100%;
  margin-top: 3px;
  padding: 15px;
  font-size: 16px;
}

.ccicon {
  width: 60px;
  height: 38px;
  position: absolute;
  top: calc(50% - 17px);
  right: 6px;
}

.ccicon img {
  width: 60px;
  height: 38px;
}

.container {
  width: 100%;
  max-width: 400px;
  height: 54vw;
  max-height: 251px;
  padding: 20px;
}

#ccsingle {
  position: absolute;
  top: 15px;
  right: 20px;
}

#ccsingle svg, #ccsingle img {
  width: 100px;
  max-height: 60px;
}

.creditcard svg#cardfront, .creditcard svg#cardback {
  border-radius: 22px;
  width: 100%;
  box-shadow: 1px 5px 6px #000;
}

#generatecard {
  cursor: pointer;
  float: right;
  color: #fff;
  cursor: pointer;
  float: right;
  background-color: #909090;
  border-radius: 4px;
  padding: 2px 4px;
  font-size: 12px;
}

.error-message-block {
  color: red;
  font-size: .8rem;
  position: absolute;
  bottom: 0;
  left: 0;
}

.creditcard .lightcolor, .creditcard .darkcolor {
  transition: fill .5s;
}

